@use "sass:map";
@use "./colors";
@use "./utils";

@tailwind base;
@tailwind components;
@tailwind utilities;

* {
  box-sizing: border-box;
}

html,
body {
  padding: 0;
  margin: 0;
  font-family: "PlusJakartaSans", sans-serif;
  background-color: colors.$black-primary;
  color: white;
}

body {
  font-size: utils.fontSize("regular");
}

main {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

a {
  color: inherit;
  text-decoration: none;
  cursor: pointer;
}

@font-face {
  font-family: "PlusJakartaSans";
  src: url("./../assets/fonts/PlusJakartaSans-Regular.woff") format("woff"),
    url("./../assets/fonts/PlusJakartaSans-Regular.woff2") format("woff2");
  font-weight: 400;
}

@font-face {
  font-family: "PlusJakartaSans";
  src: url("./../assets/fonts/PlusJakartaSans-SemiBold.woff") format("woff"),
    url("./../assets/fonts/PlusJakartaSans-SemiBold.woff2") format("woff2");
  font-weight: 500;
}

@font-face {
  font-family: "PlusJakartaSans";
  src: url("./../assets/fonts/PlusJakartaSans-Bold.woff") format("woff"),
    url("./../assets/fonts/PlusJakartaSans-Bold.woff2") format("woff2");
  font-weight: 700;
}

@media (max-width: utils.screenSize("xl")) {
  html {
    font-size: 80%;
  }
}

@media (max-width: utils.screenSize("md")) {
  html {
    font-size: 70%;
  }
}
